<template class="container">
  <header class="header">
    <h1 class="header__title">Pokenomicon</h1>
    <div class="header__quiz">
      <button
        class="header__button"
        @click="onQuiz">
        <img 
          class="header__gif"
          src="./img/pikachu-what.gif" alt="" width="100">
        <span class="header__span">
          Это что за<br> покемон?
        </span>
      </button>
    </div>
  </header>
  <main>
    <section 
      class="section">
      <form 
        v-if="!quizMode"
        class="search__field" 
        @submit.prevent="this.luck ? onRandom() : add()">
        <label 
          class="search__label" 
          for="search">{{this.luck ? 'Кто же внутри?' : 'Найти покемона'}}</label>
        <input 
          v-if="!this.luck"
          class="serach__input"
          type="text" 
          id="search"
          v-on:keydown.enter="add"
          v-model.trim="card"
          placeholder="Введите имя или ID покемона"
          pattern="^[a-zA-Z0-9]+$"
          required
          >
        <div class="search__buttons-field">
          <button
            type="submit"
            class="search__button"
            :class="this.luck ? 'search__button--random' : ''"
          ></button>
          <button
            class="shiny__button"
            v-on:click.prevent="onShiny"
            v-if="!this.luck"
            :class="this.shiny ? 'shiny__button__active' : ''"
            ></button>
          <button
            class="luck__button"
            v-on:click.prevent="onLuck"
            :class="this.luck ? 'luck__button__active' : ''"
            ></button>
        </div>
      </form>

      <label
        class="releaseAll__button"
        v-if="this.cards.length > 0 " 
        @click="deleteAll()">
          <input type="checkbox" id="release">Отпустить всех
          <span></span>
      </label>
      <!-- <button @click="onFight()">В бой</button> -->

      <div 
        class="quiz"
        v-if="quizMode">
        <!-- <form 
          class="quiz__form"
          @submit.prevent.enter="quiz()">
          <img 
            class="quiz__image"
            :class="[hasAnswer ? 'quiz__image--answer': '']"
            v-if="quizItem.image" 
            v-bind:src="quizItem.image" 
            alt="shadow">
          <input 
            class="quiz__input"
            type="text"
            v-model.trim="quizAnswer"
            v-if="quizItem.image"
            placeholder="Введите имя покемона">
        </form> -->
        <div class="quiz__form">
          <img 
            class="quiz__image"
            :class="[hasAnswer ? 'quiz__image--answer': '']"
            v-if="quizItem.image" 
            v-bind:src="quizItem.image" 
            alt="shadow">
          <div class="quiz__answer-list">
            <button 
              class="quiz__answer-item" 
              v-for="(item, idx) in quizList"
              v-bind:key="idx"
              :class="[select == item ? 'green' : '' ]"
              @click="quiz(item)">{{item.substring(0, 1).toUpperCase() + item.slice(1)}}</button>
          </div>
        </div>


        <div class="quiz__buttons">
          <button 
            class="quiz__question"
            @click="createQuiz">Это что за <br> покемон?
          </button>
          <button 
            class="quiz__help"
            @click="onHelp"
            v-if="quizItem.image"> 
            <img 
              src="./img/pokemon-bulbasaur.gif" alt="Показать ответ"
              width="70">
          </button>
        </div>
      </div>

      <div class="search__list">
      <div
        v-for="(item, idx) in cards"
        v-bind:key="idx"
        class="search__list">
        <div class="search__item item">
          <div class="image__wrapper">
            <img alt="sprite" 
            v-bind:src="item.image"
            class="item__image">
            <img
              v-if="item.shiny" 
              src="../src/img/spark.png" 
              alt="spark"
              class="item__image--shiny">
          </div>
          <ul
            class="item__properties" 
            v-bind:style="`background: linear-gradient(${this.colors[`${item.type1}`]}, ${this.colors[`${item.type2 ? `${item.type2}` : `${item.type1}`}`]})`">
            <li>#{{item.id}}</li>
            <li>Имя: {{ item.name }}</li>
            <li>Тип: {{ item.type1 }} {{ item.type2 }} </li>
            <li>Рост: {{ (item.height * 0.1).toFixed(1) }} м </li>
            <li>Вес: {{ (item.weight * 0.1).toFixed(1) }} кг </li>
          </ul>
          <button
            @click="handleDelete(item)"
            class="item__button">Отпустить</button>
        </div>
      </div>
      </div>
    </section>
  </main>
  <footer class="footer">
    <a href="https://vk.com/rushinyhunt" target="_blank">
    <div class="footer__social">
      <img src="../src/img/boy.png" alt="boy" class="footer__boy">
      <img src="../src/img/bush.png" alt="bush" class="footer__bush">
    </div>
    </a>
    <p>Пробная версия</p>
  </footer>
</template>

<script>


export default {
  data() {
    return {
      card: "",
      cards: [],
      shiny: false,
      luck: false,
      colors: {
        fire: '#fa5542',
        grass: '#8bd750',
        electric: '#fde739',
        water: '#55aefe',
        ground: '#edcd56',
        rock: '#cdbb71',
        fairy: '#fbacfd',
        poison: '#aa5da3',
        bug: '#c3d21f',
        dragon: '#8b77fc',
        psychic: '#ff62b6',
        flying: '#78a3ff',
        fighting: '#a55848',
        normal: '#bbbcac',
        ice: '#95f1fe',
        dark: '#8e6956',
        ghost: '#7873d9',
        steel: '#c4c2da'
      },
      quizItem: {
        name: '',
        image: '',
        answer: '',
      },
      quizList: [],
      quizAnswer: '',
      quizMode: false,
      hasAnswer: '',
      select: null,
    };
  },

  // created() {
  //   const cardsData = localStorage.getItem('pokenomicon-list');

  //   if (cardsData) {
  //     this.cards = JSON.parse(cardsData);
  //     this.cards.forEach(item => {
  //       this.subscribeToUpdates(item);
  //     })
  //   }
  // },

  methods: {
    onShiny() {
      this.shiny = !this.shiny;
    },

    onLuck() {
      this.luck = !this.luck;
    },

    subscribeToUpdates(newCard) {
      setTimeout(async () => {
        
        try {
        let response = await fetch(
          `https://pokeapi.co/api/v2/pokemon/${newCard.name}/`
        );
        let data = await response.json();
        
        this.cards.find(item => item.name === newCard.name).name = `${data.name.substring(0, 1).toUpperCase() + data.name.slice(1)}`;
        this.cards.find(item => item.id === newCard.id).id = data.id;
        this.cards.find(item => item.weight === newCard.weight).weight = data.weight;
        this.cards.find(item => item.height === newCard.height).height = data.height;
        this.cards.find(item => item.image=== newCard.image).image = newCard.shiny ? data.sprites.front_shiny : data.sprites.front_default;
        if (data.types.length > 1) {
          this.cards.find(item => item.type1 === newCard.type1).type1 = data.types[0].type.name;
          this.cards.find(item => item.type2 === newCard.type2).type2 = data.types[1].type.name;
        } else {
          this.cards.find(item => item.type1 === newCard.type1).type1 = data.types[0].type.name;
          this.cards.find(item => item.type2 === newCard.type2).type2 = null;
        
        }
        } catch {
          this.cards.pop(newCard);
          alert('Такой покемон не найден!');
        }

        
      }, 0); 
      
    },

    add() {
      const newCard = {
        image : '',
        name: this.card.toLowerCase(),
        type1: '',
        type2: '',
        height: '',
        weight: '',
        id: '',
        shiny: this.shiny,
      };

      if(this.card.length < 1 || this.card < 1 || this.card > 898) {
        return alert('Введите имя покемона латинсткими буквами или ID (от 1 до 898)');
      } 

      this.cards.push(newCard);
      this.card = '';

      localStorage.setItem('pokenomicon-list', JSON.stringify(this.cards));
      this.subscribeToUpdates(newCard);
    },

    handleDelete(cardToRemove) {
      this.cards = this.cards.filter(item => item != cardToRemove);
    },

    onRandom() {
      const randomCard = {
        name: Math.floor(Math.random() * 898),
        shiny: Math.floor(Math.random() * 100) > 90 ? this.shiny = true : this.shiny = false,
      };

      this.cards.push(randomCard);

      this.subscribeToUpdates(randomCard);
    },

    quiz(item) {

      if (item == this.quizItem.name) {
        this.hasAnswer = true;
        this.select = item;
      } else {
        alert('Неправильный ответ!');
        this.select = false;
      }
    },

    onQuiz() {
      this.quizMode = !this.quizMode;
      this.cards = [];
    },
    //   Карточка с со строкой для ответа
    // async createQuiz() {
    //   let response = await fetch(`https://pokeapi.co/api/v2/pokemon/${Math.floor(Math.random() * 898)}`);
    //   let data = await response.json();

    //   this.quizItem.image = data.sprites.front_default;
    //   this.quizItem.name = data.name;
    //   this.hasAnswer = false;
    //   this.quizAnswer = '';
    // },

    async addQuizList() {
      
      while (this.quizList.length < 4) {
      let response = await fetch(`https://pokeapi.co/api/v2/pokemon/${Math.floor(Math.random() * 898)}`);
      let data = await response.json();

      this.quizList.push(data.name);
      }
    },

    async createQuiz() {
      this.quizList = [];
      await this.addQuizList();
        let response = await fetch(`https://pokeapi.co/api/v2/pokemon/${this.quizList[Math.floor(Math.random() * 4)]}`);
      let data = await response.json();

      this.quizItem.image = data.sprites.front_default;
      this.quizItem.name = data.name;
      this.hasAnswer = false;
      
    },

    onHelp() {
      alert(this.quizItem.name);
    },

    //  async onFight() {
    //   if (this.cards[0].weight > this.cards[1].weight) {
    //     this.cards.pop();
    //   } else {
    //     this.cards.shift();
    //   }
      
    //   let response = await fetch('https://pokeapi.co/api/v2/pokemon/1/');
    //   let data = await response.json();

    //   console.log(data);
    // },

    deleteAll() {
      setTimeout(() => { 
        this.cards.length = 0
      },
      1000);
      setTimeout(() => {
        document.getElementById('release').checked = false
      },
      950)
    },


    
  },

  
};
</script>

<style src="./style.css"></style>
